import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Avatar from '@material-ui/core/Avatar';
import orange from '@material-ui/core/colors/deepOrange';
import green from '@material-ui/core/colors/green';

import { getActiveUserData, getAddressAbbreviation } from '../utils/crypto';
import { loadTransactions, getRollHistory } from '../utils/mixin';

const styles = {
  orangeAvatar: {
    color: '#fff',
    fontSize: 8,
    width: 25,
    height: 25,
    backgroundColor: orange[500],
  },
  greenAvatar: {
    color: '#fff',
    fontSize: 8,
    width: 25,
    height: 25,
    backgroundColor: green[500],
  },
  orangeColor: {
    color: orange[500],
  },
  greenColor: {
    color: green[500],
  },
};

const CustomTableCell = withStyles(theme => ({
  head: {
    minWidth: 10,
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
    padding: '0px 0px 0px 0px',
  },
  body: {
    minWidth: 10,
    fontSize: 14,
    color: theme.palette.common.white,
    padding: '0px 0px 0px 0px',
  },
}))(TableCell);

class Transactions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    const { activeAccount } = this.props;
    if (activeAccount) {
      this.loadTransactions(activeAccount);
    } else {
      this.setState({ rolls: [] });
    }
  }

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if (
      this.props.activeAccount !== prevProps.activeAccount ||
      this.props.reloadAccountNonce !== prevProps.reloadAccountNonce
    ) {
      if (this.props.activeAccount) {
        this.loadTransactions(this.props.activeAccount);
      } else {
        this.setState({ rolls: [] });
      }
    }
  }

  loadTransactions = async activeAccount => {
    const userData = await getActiveUserData(activeAccount);
    const transactions = await loadTransactions(userData);
    this.setState({ transactions });

    const rolls = await getRollHistory(transactions);
    this.setState({ rolls });
  };

  renderAvatarColor(win) {
    if (win) {
      return styles.greenAvatar;
    }
    return styles.orangeAvatar;
  }

  renderFontColor(win) {
    if (win) {
      return styles.greenColor;
    }
    return styles.orangeColor;
  }

  renderAvatar(win) {
    if (win) {
      return 'Win';
    }
    return 'Lose';
  }

  renderFromOrTo(amount, opponentId) {
    if (amount > 0) {
      return `From: ${getAddressAbbreviation(opponentId)}`;
    }
    return `To: ${getAddressAbbreviation(opponentId)}`;
  }

  renderTransactionsTable() {
    const { classes } = this.props;

    const { rolls } = this.state;
    const { assets } = this.props;
    if (!assets || assets.length === 0) {
      return null;
    }

    return (
      <Grid container spacing={24}>
        <Grid item xs={12} md={12} lg={12}>
          <Card className="card transactions-card">
            <Typography variant="h6" color="secondary">
              My play history
            </Typography>
            <Table className={classes.table}>
              <TableHead>
                <TableRow>
                  <CustomTableCell className="transactions-in-out" />
                  <CustomTableCell className="transactions-in-out" numeric>
                    Prediction
                  </CustomTableCell>
                  <CustomTableCell className="transactions-in-out" numeric>
                    Lucky Number
                  </CustomTableCell>
                  <CustomTableCell className="transactions-in-out" numeric>
                    Currency
                  </CustomTableCell>
                  <CustomTableCell className="transactions-in-out" numeric>
                    Bet
                  </CustomTableCell>
                  <CustomTableCell className="transactions-in-out" numeric>
                    Payout
                  </CustomTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rolls.map(roll => {
                  const amount = roll.amount;
                  const positiveAmount = amount > 0 ? amount : -amount;
                  let positiveAmountDisplay = positiveAmount;
                  if (
                    Math.round(positiveAmount) !== parseFloat(positiveAmount)
                  ) {
                    positiveAmountDisplay = parseFloat(positiveAmount).toFixed(
                      3
                    );
                  } else {
                    positiveAmountDisplay = positiveAmount;
                  }

                  const win = roll.win;
                  const avatar = this.renderAvatar(win);
                  const avatarColor = this.renderAvatarColor(win);
                  const fontColor = this.renderFontColor(win);

                  const playerNumber = roll.playerNumber;
                  const luckyNumber = roll.luckyNumber;
                  const asset = assets.find(a => a.asset_id === roll.asset_id);
                  const icon = asset && asset.icon_url;
                  const symbol = asset && asset.symbol;
                  let payout = roll.payout;

                  let payoutDisplay = payout;
                  if (Math.round(payout) !== parseFloat(payoutDisplay)) {
                    payoutDisplay = parseFloat(payout).toFixed(3);
                  }
                  if (payoutDisplay === 0) {
                    payoutDisplay = '';
                  }

                  return (
                    <TableRow key={roll.snapshot_id}>
                      <CustomTableCell className="transactions-icon-image">
                        {' '}
                        <Avatar style={avatarColor}>{avatar}</Avatar>{' '}
                      </CustomTableCell>
                      <CustomTableCell className="transactions-in-out" numeric>
                        {playerNumber}
                      </CustomTableCell>
                      <CustomTableCell className="transactions-in-out" numeric>
                        <span style={fontColor}>{luckyNumber}</span>
                      </CustomTableCell>
                      <CustomTableCell className="transactions-in-out" numeric>
                        <span>
                          <img
                            src={icon}
                            alt={symbol}
                            className="transactions-icon-image"
                          />
                        </span>
                        <span> {symbol} </span>
                      </CustomTableCell>

                      <CustomTableCell className="transactions-amount" numeric>
                        {positiveAmountDisplay}
                      </CustomTableCell>

                      <CustomTableCell className="transactions-amount" numeric>
                        {payoutDisplay}
                      </CustomTableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </Card>
        </Grid>
      </Grid>
    );
  }

  render() {
    const { activeAccount } = this.props;
    const { rolls } = this.state;

    if (!rolls) {
      return (
        <div className="cards">
          <Card className="card transactions-card">
            <p>Loading my bets...</p>
          </Card>
        </div>
      );
    } else if (rolls.length === 0) {
      if (activeAccount) {
        return (
          <div className="cards">
            <Card className="card transactions-card">
              <p>No betting history, start betting now!</p>
            </Card>
          </div>
        );
      } else {
        return <div />;
      }
    }
    return <div className="cards">{this.renderTransactionsTable()}</div>;
  }
}

Transactions.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  activeAccount: state.account.activeAccount,
  assets: state.account.assets,
  reloadAccountNonce: state.wallet.reloadAccountNonce,
});

export default connect(mapStateToProps)(withStyles(styles)(Transactions));
