import React, { Component } from 'react';
import { connect } from 'react-redux';

import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import IconButton from '@material-ui/core/IconButton/IconButton';
import AppBar from '@material-ui/core/AppBar/AppBar';
import PermIdentity from '@material-ui/icons/PermIdentity';
import diceImg from '../images/dice.svg';

import {
  SCREEN_MY_BET,
  SCREEN_MY_WALLET,
  SCREEN_UNLOCK_PIN,
  setPasswordHash,
  setScreen,
} from '../actions/app';

class WalletAppBar extends Component {
  myBet = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_BET);
  };

  myWallet = () => {
    const { setScreen, mixinPin, activeAccount } = this.props;

    if (activeAccount) {
      if (!mixinPin) {
        const { setScreen } = this.props;
        setScreen(SCREEN_UNLOCK_PIN);
      } else {
        setScreen(SCREEN_MY_WALLET);
      }
    } else {
      setScreen(SCREEN_MY_WALLET);
    }
  };

  render() {
    return (
      <AppBar position="sticky">
        <Toolbar>
          <div className="tool-bar">
            <div className="logo-container">
              <h2>
                <div className="trademark">LuckyBet </div>
              </h2>
              <Tooltip title="Home - Betting">
                <IconButton
                  color="inherit"
                  onClick={this.myBet}
                  aria-label="Betting"
                >
                  <img
                    className="dice-img"
                    src={diceImg}
                    alt="Home - Betting"
                  />
                </IconButton>
              </Tooltip>
            </div>

            <div />

            <div>
              <a href="whitepaper.pdf" className="menulink" target="_blank">
                whitepaper
              </a>
              <Tooltip title="My Account">
                <IconButton
                  color="inherit"
                  onClick={this.myWallet}
                  aria-label="My account"
                >
                  <PermIdentity className="account-details-button-icon" />
                </IconButton>
              </Tooltip>
            </div>
          </div>
        </Toolbar>
      </AppBar>
    );
  }
}

const mapStateToProps = state => ({
  activeAccount: state.account.activeAccount,
  passwordHashInBackground: state.app.passwordHashInBackground,
  passwordHashInStorage: state.app.passwordHashInStorage,
  mixinPin: state.wallet.mixinPin,
});

const mapDispatchToProps = {
  setPasswordHash,
  setScreen,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WalletAppBar);
