import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import IconButton from '@material-ui/core/IconButton/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import Typography from '@material-ui/core/Typography/Typography';
import TextField from '@material-ui/core/TextField/TextField';
import Button from '@material-ui/core/Button/Button';
import Send from '@material-ui/icons/Send';
import LinearProgress from '@material-ui/core/LinearProgress';
import FormControl from '@material-ui/core/FormControl';

import { getActiveUserData, isAddress } from '../utils/crypto';

import Transition from './Transition';

import { hideWithdrawToken, reloadAccount } from '../actions/wallet';
import { showSnackbar } from '../actions/snackbar';
import { withdraw } from '../utils/mixin';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};

class WithdrawToken extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      withdrawTo: '',
      withdrawMemo: '',
      withdrawAmount: '',
      withdrawPin: '',
      isLoading: false,
    };
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleAmountChange = event => {
    const formattedAmount = event.target.value
      .replace(/^0+(?!\.|$)/, '')
      .replace(/[^0-9 .]+/g, '')
      .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1');

    this.setState({ [event.target.name]: formattedAmount });
  };

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    if (value.length > 6) {
      value = value.substring(0, 6);
    }
    this.setState({ [event.target.name]: value });
  };

  disableSubmit = () => {
    return !this.isWithdrawValid();
  };

  isWithdrawValid = () => {
    const { withdrawTo, withdrawAmount, isLoading, withdrawPin } = this.state;

    return (
      !isLoading &&
      withdrawTo &&
      isAddress(withdrawTo) &&
      withdrawAmount > 0 &&
      withdrawPin.length === 6
    );
  };

  isWithdrawing = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  withdrawToken = async () => {
    const {
      withdrawTo,
      withdrawMemo,
      withdrawAmount,
      withdrawPin,
    } = this.state;
    this.setState({ isLoading: true });

    const {
      asset,
      showSnackbar,
      hideWithdrawToken,
      activeAccount,
      reloadAccount,
    } = this.props;

    const userData = await getActiveUserData(activeAccount);
    const withdrawData = {
      withdrawTo,
      withdrawMemo,
      withdrawAmount,
      withdrawPin,
    };
    try {
      showSnackbar('Submitting withdraw transaction, please wait...');
      await withdraw(asset, userData, withdrawData);
      this.setState({
        isLoading: false,
        withdrawTo: '',
        withdrawMemo: '',
        withdrawAmount: '',
        withdrawPin: '',
      });
      reloadAccount();
      showSnackbar('Withdraw successfully!');
      hideWithdrawToken();
    } catch (error) {
      this.setState({ isLoading: false });
      console.error(error);
      showSnackbar(`Withdraw failed: ${error.description}`);
    }
  };

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  renderWithdrawing() {
    if (this.isWithdrawing()) {
      return (
        <div className="loading-info">
          <p>Submitting withdraw transaction, please wait...</p>
          <LinearProgress />
        </div>
      );
    }

    return null;
  }

  render() {
    const {
      open,
      asset,
      hideWithdrawToken,
      activeAccount,
      classes,
    } = this.props;
    if (!asset || !activeAccount) {
      return null;
    }

    const {
      withdrawTo,
      withdrawMemo,
      withdrawAmount,
      withdrawPin,
      showPassword,
    } = this.state;

    const balance = `${asset.balance || 0} ${asset.symbol}`;
    const addressLabel =
      asset.symbol === 'EOS' ? 'Account name' : 'Account address';

    return (
      <Dialog
        fullScreen
        open={open}
        onClose={hideWithdrawToken}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={hideWithdrawToken}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              Withdraw
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
          <div className="card withdraw-card">
            <div className="withdraw-summary">
              <img src={asset.icon_url} alt={asset.name} />
              <p>{balance}</p>
            </div>
            <TextField
              required
              name="withdrawTo"
              autoComplete="off"
              label={addressLabel}
              className="withdraw-to"
              value={withdrawTo}
              onChange={this.handleChange}
              disabled={this.isWithdrawing()}
              InputLabelProps={{
                classes: {
                  root: classes.cssLabel,
                },
              }}
              InputProps={{
                width: 20,
                classes: {
                  root: classes.cssOutlinedInput,
                  notchedOutline: classes.notchedOutline,
                },
              }}
              variant="outlined"
            />
            <TextField
              name="withdrawMemo"
              label="Memo"
              className="withdraw-memo"
              autoComplete="off"
              value={withdrawMemo}
              onChange={this.handleChange}
              margin="normal"
              InputLabelProps={{
                classes: {
                  root: classes.cssLabel,
                },
              }}
              InputProps={{
                width: 20,
                maxLength: 140,
                classes: {
                  root: classes.cssOutlinedInput,
                  notchedOutline: classes.notchedOutline,
                },
              }}
              variant="outlined"
              disabled={this.isWithdrawing()}
            />
            <TextField
              required
              name="withdrawAmount"
              label="Withdrawal Amount"
              className="withdraw-amount"
              autoComplete="off"
              value={withdrawAmount}
              onChange={this.handleAmountChange}
              margin="normal"
              type="number"
              placeholder="0.00"
              InputLabelProps={{
                classes: {
                  root: classes.cssLabel,
                },
              }}
              InputProps={{
                width: 20,
                classes: {
                  root: classes.cssOutlinedInput,
                  notchedOutline: classes.notchedOutline,
                },
              }}
              variant="outlined"
              disabled={this.isWithdrawing()}
            />
            <FormControl className="withdraw-pin" margin="normal">
              <TextField
                required
                label="Your 6 digits PIN"
                name="withdrawPin"
                autoComplete="off"
                type={showPassword ? 'text' : 'password'}
                value={withdrawPin}
                onChange={this.handlePinChange}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  maxLength: 6,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                }}
                placeholder="Your 6 digits PIN"
                variant="outlined"
                disabled={this.isWithdrawing()}
              />
            </FormControl>
            <Button
              id="withdraw-token-button"
              className="withdraw-token-button actionbutton"
              variant="contained"
              color="secondary"
              disabled={this.disableSubmit()}
              onClick={this.withdrawToken}
            >
              Withdraw <Send className="withdraw-button-icon" />
            </Button>
            {this.renderWithdrawing()}
          </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.withdrawTokenOpen,
  activeAccount: state.account.activeAccount,
  asset: state.wallet.asset,
});

const mapDispatchToProps = {
  hideWithdrawToken,
  showSnackbar,
  reloadAccount,
};

WithdrawToken.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(WithdrawToken));
