import React from 'react';

import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import InputLabel from '@material-ui/core/InputLabel/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl/FormControl';
import LockOpen from '@material-ui/icons/LockOpen';
import DeleteForever from '@material-ui/icons/DeleteForever';
import Button from '@material-ui/core/Button/Button';

import WalletReset from './WalletReset';
import { showWalletReset } from '../actions/wallet';

import logoDice from '../images/dice.png';
import { showSnackbar } from '../actions/snackbar';
import { getActiveUserData } from '../utils/crypto';
import { verifyPin } from '../utils/mixin';
import { setMixinPin } from '../actions/wallet';
import { SNACKBAR_ERROR } from './WalletSnackbar';

import { SCREEN_MY_BET, SCREEN_MY_WALLET, setScreen } from '../actions/app';

import customTheme from './customTheme';

const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
  },
};

class UnlockPin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mixinPin: '',
      mixinPinUpdated: false,
      isLoading: false,
    };
  }

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    if (value.length > 6) {
      value = value.substring(0, 6);
    }
    this.setState({ [event.target.name]: value });

    this.setState({ changingClass: 'logoSpin' });
  };

  disableSubmit = () => {
    const { mixinPin } = this.state;
    return !mixinPin || mixinPin.length < 6;
  };

  unlock = async () => {
    this.setState({ isLoading: true });
    const { activeAccount, showSnackbar, setMixinPin } = this.props;
    const { mixinPin } = this.state;
    const userData = await getActiveUserData(activeAccount);
    try {
      await verifyPin(mixinPin, userData);
      this.setState({ isLoading: false });
      setMixinPin(mixinPin);
      const mixinPinUpdated = true;
      this.setState({ mixinPinUpdated });
    } catch (e) {
      console.error(e);
      this.setState({ changingClass: '' });
      this.setState({ isLoading: false });
      showSnackbar(`Fail to unlock account: ${e.description}`, SNACKBAR_ERROR);
    }
  };

  myBet = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_BET);
  };

  myWallet = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_WALLET);
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  render() {
    const { activeAccount, classes, showWalletReset } = this.props;
    const { mixinPinUpdated, changingClass } = this.state;
    if (!activeAccount) {
      return null;
    }

    const { mixinPin, showPassword } = this.state;

    if (mixinPinUpdated) {
      return (
        <div className="card">
          <div className="unlock-card">
            <div color="primary" className="card game-card">
              <Typography variant="h6" color="inherit">
                <span className="glowing-pink white">
                  Welcome back! Your account is unlocked.
                </span>
              </Typography>
              <div className="card logo-container">
                <img src={logoDice} alt="LuckyBet" className="token-logo-lg" />
              </div>
              <div className="space" />
              <Grid container spacing={24}>
                <Grid item xs={6} md={6} lg={6}>
                  <div color="primary" className="card game-card">
                    <Button
                      id="switch-wallet-button"
                      className="send-token-button"
                      variant="contained"
                      color="secondary"
                      size="large"
                      onClick={this.myBet}
                    >
                      Start Betting
                    </Button>
                  </div>
                </Grid>
                <Grid item xs={6} md={6} lg={6}>
                  <div color="primary" className="card game-card">
                    <Button
                      id="switch-wallet-button"
                      className="send-token-button"
                      variant="contained"
                      color="primary"
                      size="large"
                      onClick={this.myWallet}
                    >
                      <span className="glowing-green">My Account</span>
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <MuiThemeProvider theme={customTheme}>
          <div className="card">
            <div className="unlock-card">
              <Typography variant="h6" color="secondary">
                <span className="glowing-pink white">
                  For security reason, please enter your PIN to unlock and
                  continue
                </span>
              </Typography>
              <div className="card logo-container">
                <div className={changingClass}>
                  <img
                    src={logoDice}
                    alt="LuckyBet"
                    className="token-logo-lg"
                  />
                </div>
              </div>
              <FormControl className="unlock-pin" margin="normal">
                <InputLabel htmlFor="mixinPin">The 6 digit PIN</InputLabel>
                <TextField
                  required
                  name="mixinPin"
                  type={showPassword ? 'text' : 'password'}
                  value={mixinPin}
                  onChange={this.handlePinChange}
                  placeholder="Enter 6 digit PIN to unlock"
                  InputLabelProps={{
                    classes: {
                      root: classes.cssLabel,
                    },
                  }}
                  InputProps={{
                    width: 20,
                    maxLength: 6,
                    classes: {
                      root: classes.cssOutlinedInput,
                      notchedOutline: classes.notchedOutline,
                    },
                    //className: classes.textField,
                  }}
                  autoComplete="off"
                  disabled={this.isSending()}
                  margin="normal"
                  color="secondary"
                  variant="outlined"
                />
              </FormControl>
              <Button
                id="send-token-button"
                className="send-token-button actionbutton"
                variant="contained"
                color="secondary"
                disabled={this.disableSubmit()}
                onClick={this.unlock}
              >
                Unlock <LockOpen className="send-button-icon" />
              </Button>
              <div className="space" />
              <Button
                size="large"
                variant="contained"
                color="secondary"
                onClick={showWalletReset}
              >
                Reset <DeleteForever className="send-button-icon" />
              </Button>
              <WalletReset />
            </div>
          </div>
        </MuiThemeProvider>
      );
    }
  }
}

const mapStateToProps = state => ({
  activeAccount: state.account.activeAccount,
});

const mapDispatchToProps = {
  showSnackbar,
  setMixinPin,
  setScreen,
  showWalletReset,
};

UnlockPin.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(UnlockPin));
