import { RESET_STATE } from './constants';

const SET_SCREEN = 'SET_SCREEN';

const SCREEN_MY_BET = 'SCREEN_MY_BET';
const SCREEN_MY_WALLET = 'SCREEN_MY_WALLET';
const SCREEN_UNLOCK_PIN = 'SCREEN_UNLOCK_PIN';

const SET_APP_SALT = 'SET_APP_SALT';
const SET_PASSWORD_HASH = 'SET_PASSWORD_HASH';

export {
  SET_SCREEN,
  SCREEN_MY_BET,
  SCREEN_MY_WALLET,
  SCREEN_UNLOCK_PIN,
  SET_APP_SALT,
  SET_PASSWORD_HASH,
};

export const setScreen = screen => ({
  type: SET_SCREEN,
  payload: {
    screen,
  },
});

export const setAppSalt = appSalt => ({
  type: SET_APP_SALT,
  payload: {
    appSalt,
  },
});

export const setPasswordHash = (
  passwordHashInBackground,
  passwordHashInStorage
) => ({
  type: SET_PASSWORD_HASH,
  payload: {
    passwordHashInBackground,
    passwordHashInStorage,
  },
});

export const resetApp = () => ({
  type: RESET_STATE,
});
