import { RESET_STATE } from '../actions/constants';
import {
  SET_SCREEN,
  SET_APP_SALT,
  SET_PASSWORD_HASH,
  SCREEN_MY_BET,
} from '../actions/app';

const initialState = {
  screen: SCREEN_MY_BET,
  appSalt: null,
  network: null,
  passwordHashInBackground: null,
  passwordHashInStorage: null,
};

export function appReducer(state = initialState, action) {
  switch (action.type) {
    case SET_SCREEN:
    case SET_APP_SALT:
    case SET_PASSWORD_HASH: {
      return {
        ...state,
        ...action.payload,
      };
    }
    case RESET_STATE:
      return initialState;
    default:
      return state;
  }
}
