import React, { Component } from 'react';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography/Typography';
import Button from '@material-ui/core/Button/Button';
import Grid from '@material-ui/core/Grid/Grid';
import VpnKey from '@material-ui/icons/VpnKey';
import Launch from '@material-ui/icons/Launch';
import AttachMoney from '@material-ui/icons/AttachMoney';

import Assets from './Assets';
import SendReceiveButtons from './SendReceiveButtons';

import { SCREEN_MY_BET, SCREEN_UNLOCK_PIN, setScreen } from '../actions/app';
import WalletReset from './WalletReset';
import { showBackupKeystore, showWalletReset } from '../actions/wallet';
import BackupKeystore from './BackupKeystore';
import ImportOrCreateAccount from './ImportOrCreateAccount';

import WalletBackup from './WalletBackup';

class MyWallet extends Component {
  myBet = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_BET);
  };

  renderWalletInfo() {
    const { showWalletReset, showBackupKeystore } = this.props;
    return (
      <div className="cards">
        <div color="primary" className="card game-card">
          <Typography variant="h4" color="secondary">
            <span className="glowing-pink white">My Account</span>
          </Typography>

          <div className="my-account">
            <Grid container spacing={24}>
              <Grid item xs={12} md={6} lg={6}>
                <Assets />
                <div className="cards">
                  <div className="card internal-transfer-card">
                    <SendReceiveButtons />
                  </div>
                </div>
              </Grid>

              <Grid item xs={12} md={6} lg={6}>
                <Grid container spacing={24}>
                  <Grid item xs={12} md={6} lg={6}>
                    <Typography variant="h6" color="primary">
                      <span className="glowing-pink white">Play Now</span>
                    </Typography>
                    <div className="space" />
                    <Button
                      className="my-wallet-button"
                      variant="contained"
                      color="secondary"
                      size="large"
                      onClick={this.myBet}
                    >
                      Start Betting <AttachMoney className="send-button-icon" />
                    </Button>
                  </Grid>
                  <Grid item xs={12} md={6} lg={6}>
                    <Typography variant="h6" color="primary">
                      <span className="glowing-pink white">Security</span>
                    </Typography>
                    <div className="space" />
                    <Button
                      className="my-wallet-button"
                      size="large"
                      variant="contained"
                      color="secondary"
                      onClick={showBackupKeystore}
                    >
                      Key Back up
                      <VpnKey className="send-button-icon" />
                    </Button>
                    <p className="small">
                      Private key is required to sign in and please keep it safe
                    </p>
                    <BackupKeystore />
                  </Grid>
                  <Grid item xs={12} md={12} lg={12}>
                    <Typography variant="h6" color="primary">
                      <span className="glowing-pink white">Sign Out</span>
                    </Typography>
                    <div className="space" />
                    <Button
                      className="my-wallet-button"
                      size="large"
                      variant="contained"
                      color="secondary"
                      onClick={showWalletReset}
                    >
                      Sign out <Launch className="send-button-icon" />
                    </Button>
                    <p className="small">
                      After you sign out, you will need to provide private key
                      and PIN for the next sign in
                    </p>
                    <WalletReset />
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </div>
        </div>

        <WalletBackup />
      </div>
    );
  }

  renderImportOrCreateAccount() {
    return <ImportOrCreateAccount />;
  }

  render() {
    const { activeAccount, mixinPin } = this.props;

    if (activeAccount) {
      if (mixinPin) {
        return this.renderWalletInfo();
      } else {
        setScreen(SCREEN_UNLOCK_PIN);
      }
    } else {
      return this.renderImportOrCreateAccount();
    }
  }
}

const mapStateToProps = state => ({
  activeAccount: state.account.activeAccount,
  mixinPin: state.wallet.mixinPin,
});

const mapDispatchToProps = {
  setScreen,
  showWalletReset,
  showBackupKeystore,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyWallet);
