import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import Button from '@material-ui/core/Button/Button';
import IconButton from '@material-ui/core/IconButton/IconButton';
import Typography from '@material-ui/core/Typography/Typography';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import CloseIcon from '@material-ui/icons/Close';

import { CopyToClipboard } from 'react-copy-to-clipboard';

import loadQRCode from '../utils/loadQRCode';

import Transition from './Transition';

import { hideReceiveToken } from '../actions/wallet';
import { showSnackbar } from '../actions/snackbar';
import { withStyles } from '@material-ui/core/styles';
const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};
class ReceiveToken extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.initQRCode();
  }

  initQRCode = async () => {
    const { activeAccount } = this.props;
    const qrCode = await loadQRCode(activeAccount.clientId);
    this.setState({ qrCode });
  };

  handleCopyToClipBoard = () => {
    const { showSnackbar } = this.props;
    showSnackbar('User ID copied to clipboard.');
  };

  render() {
    const { activeAccount, open, hideReceiveToken } = this.props;
    const { qrCode } = this.state;
    const { clientId } = activeAccount;

    if (!activeAccount || !qrCode || !clientId) {
      return null;
    }

    return (
      <Dialog
        fullScreen
        open={open}
        onClose={hideReceiveToken}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={hideReceiveToken}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              Receive
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
          <div className="card account-details-card">
            <Typography variant="h6">
              <span class="glowing-pink white">
                Send to this User ID on Mixin
              </span>
            </Typography>
            <div className="space" />
            {qrCode && (
              <img src={qrCode} alt="account address" className="qr-code" />
            )}
            <div className="account-details-address">
              <div className="address white">
                <div className="space" />
                {clientId}
                <div className="space" />
                <CopyToClipboard
                  text={clientId}
                  onCopy={this.handleCopyToClipBoard}
                >
                  <Button
                    id="copy-user-id"
                    color="secondary"
                    variant="contained"
                  >
                    Copy User ID{' '}
                    <FileCopyIcon className="copy-private-key-icon" />
                  </Button>
                </CopyToClipboard>
              </div>
            </div>
          </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.receiveTokenOpen,
  activeAccount: state.account.activeAccount,
});

const mapDispatchToProps = {
  hideReceiveToken,
  showSnackbar,
};

ReceiveToken.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(ReceiveToken));
