const MIXIN_URL = getNodeUrl(MAINNET);
const TIMEOUT = 3600;
const mixinUtils = new MixinUtils();

const getRequestSignature = (method, uri, body) => {
  if (!body) {
    body = '';
  }
  const payload =
    method +
    uri +
    (typeof body === 'object' ? JSON.stringify(body) : body.toString());
  const signature = crypto
    .createHash('sha256')
    .update(payload)
    .digest('hex');
  return signature;
};

const getJwtToken = (clientId, sessionId, privateKey, method, uri, body) => {
  const issuedAt = Math.floor(Date.now() / 1000);
  const expireAt = issuedAt + TIMEOUT;
  const payload = {
    uid: clientId,
    sid: sessionId,
    iat: issuedAt,
    exp: expireAt,
    jti: uuid(),
    sig: getRequestSignature(method, uri, body),
  };
  const token = jwt.sign(payload, privateKey, { algorithm: 'RS512' });
  return token;
};

const signPin = (pin, userData) => {
  return mixinUtils.signEncryptedPin(
    pin,
    userData.pinToken,
    userData.sessionId,
    userData.privateKey
  );
};

const error = (resp, callback) => {
  console.error(resp);
};

const send = (token, method, url, params, callback) => {
  const requestObject = {
    type: method,
    url: url,
    contentType: 'application/json',
    beforeSend: xhr => {
      xhr.setRequestHeader('Authorization', 'Bearer ' + token);
    },
    success: resp => {
      let consumed = false;
      if (typeof callback === 'function') {
        consumed = callback(resp);
      }
      if (!consumed && resp.error !== null && resp.error !== undefined) {
        error(resp);
      }
    },
    error: event => {
      error(event.responseJSON, callback);
    },
  };

  if (params) {
    requestObject.data = JSON.stringify(params);
  }
  $.ajax(requestObject);
};

const sendApi = (method, url, params, callback) => {
  const requestObject = {
    type: method,
    url: url,
    contentType: 'application/json',
    success: resp => {
      let consumed = false;
      if (typeof callback === 'function') {
        consumed = callback(resp);
      }
      if (!consumed && resp.error !== null && resp.error !== undefined) {
        error(resp);
      }
    },
    error: event => {
      error(event.responseJSON, callback);
    },
  };

  if (params) {
    requestObject.data = JSON.stringify(params);
  }
  $.ajax(requestObject);
};

const request = (method, path, params, userData, callback) => {
  const url = MIXIN_URL + path;
  let token = '';
  if (userData) {
    token = getJwtToken(
      userData.clientId,
      userData.sessionId,
      userData.privateKey,
      method,
      path,
      params
    );
  } else {
    token = getJwtToken(
      config.clientId,
      config.sessionId,
      config.privateKey,
      method,
      path,
      params
    );
  }
  return send(token, method, url, params, callback);
};

const requestApi = (method, path, params, userData, callback) => {
  const url = apiUrl + path;
  let token = '';
  let clientId = '';
  if (userData) {
    token = getJwtToken(
      userData.clientId,
      userData.sessionId,
      userData.privateKey,
      method,
      path,
      params
    );
    clientId = userData.clientId;
  }

  // payload for Api endpoint
  let payload = {
    token: token,
    params: params,
    clientId: clientId,
  };

  return sendApi('POST', url, payload, callback);
};

export const createUserProxy = () => {
  return new Promise((resolve, reject) => {
    requestApi('POST', '/users', {}, null, resp => {
      if (resp.error) {
        reject(resp.error);
        return;
      }
      resolve(resp.data);
    });
  });
};

// https://developers.mixin.one/api/alpha-mixin-network/create-pin/
export const updatePin = (userData, oldPin, newPin) => {
  let old_pin = '';
  if (oldPin !== '') {
    old_pin = signPin(oldPin, userData);
  }
  const params = {
    old_pin: old_pin,
    pin: signPin(newPin, userData),
  };

  return new Promise((resolve, reject) => {
    request('POST', '/pin/update', params, userData, resp => {
      if (resp.error) {
        reject(resp.error);
        return;
      }
      resolve(resp.data);
    });
  });
};

// https://developers.mixin.one/api/alpha-mixin-network/read-assets/
const loadAssets = userData => {
  return new Promise((resolve, reject) => {
    request('GET', '/assets', undefined, userData, resp => {
      if (resp.error) {
        reject(resp.error);
        return;
      }
      resolve(resp.data);
    });
  });
};

const sortAssets = assets => {
  return assets.sort((a, b) => {
    if (!a.balance && !b.balance) {
      return 0;
    } else if (!b.balance) {
      return -1;
    } else {
      return b.balance - a.balance;
    }
  });
};

export const loadFakePresetAssets = async userData => {
  const assets = presetAssets;

  // Fake assets with 100 CNB
  for (let i in assets) {
    if (assets[i].asset_id === CNB_ASSET_ID) {
      assets[i].balance = 100;
      break; //Stop this loop, we found it!
    }
  }
  return sortAssets(assets);
};

export const loadAssetsWithPreset = async userData => {
  const assets = await loadAssets(userData);
  let mergedAssets = presetAssets;
  for (const asset of assets) {
    // remove the preset one, then re-add
    mergedAssets = mergedAssets.filter(a => a.asset_id !== asset.asset_id);
    mergedAssets.push(asset);
  }
  return sortAssets(mergedAssets);
};

