// No doc yet
export const getRollHistory = transactions => {
  return new Promise((resolve, reject) => {
    let arrayRolls = [];

    transactions.forEach(function(transaction) {
      try {
        const memo = formatMemo(transaction.memo);
        if (memo) {
          var roll = {};
          roll.created_at = transaction.created_at;
          roll.snapshot_id = transaction.snapshot_id;
          roll.asset_id = transaction.asset_id;
          roll.amount = transaction.amount;
          roll.type = memo.type;
          roll.game = memo.game;
          roll.playerNumber = memo.playerNumber;
          roll.originalSnapshotId = memo.originalSnapshotId;
          roll.luckyNumber = memo.luckyNumber;
          arrayRolls.push(roll);
        }
      } catch (e) {
        // do not push to array
      }
    });

    let arrayMergedRolls = [];

    arrayRolls.forEach(function(roll) {
      try {
        if (roll.type === 'transfer') {
          // player transfer, then search to find if there is a payout
          let mergedRoll = {};
          mergedRoll.created_at = roll.created_at;
          mergedRoll.snapshot_id = roll.snapshot_id;
          mergedRoll.asset_id = roll.asset_id;
          mergedRoll.amount = Math.abs(roll.amount).toString();
          mergedRoll.playerNumber = roll.playerNumber;

          var findPayout = false;
          arrayRolls.forEach(function(otherRoll) {
            if (
              otherRoll.originalSnapshotId === mergedRoll.snapshot_id &&
              otherRoll.asset_id === mergedRoll.asset_id
            ) {
              // find matching
              mergedRoll.payout = otherRoll.amount; // payout Amount;
              mergedRoll.luckyNumber = otherRoll.luckyNumber; // lucky number
              mergedRoll.win = true;
              findPayout = true;
            }
          });

          if (!findPayout) {
            mergedRoll.win = false;
            mergedRoll.luckyNumber = calculateHashRandom(
              mergedRoll.snapshot_id
            );
            mergedRoll.payout = 0;
          }

          arrayMergedRolls.push(mergedRoll);
        }
      } catch (e) {
        // do not push to array
      }
    });

    resolve(arrayMergedRolls);
  });
};

function formatMemo(data) {
  let result = null;
  try {
    const memo = Buffer.from(data, 'base64').toString();
    const jsonMemo = JSON.parse(memo);
    if (jsonMemo) {
      if (jsonMemo.g && jsonMemo.n) {
        // transfer
        result = {
          type: 'transfer',
          game: jsonMemo.g,
          playerNumber: jsonMemo.n,
        };
        return result;
      }

      if (jsonMemo.s && jsonMemo.l) {
        // transfer
        result = {
          type: 'payout',
          originalSnapshotId: jsonMemo.s,
          luckyNumber: jsonMemo.l,
        };
        return result;
      }
    } else {
      result = null;
    }
  } catch (e) {
    result = null;
  }
  return result;
}

