import React from 'react';
import { connect } from 'react-redux';

import { showNetwork } from '../actions/appBar';
import {
  SCREEN_MY_BET,
  SCREEN_MY_WALLET,
  SCREEN_UNLOCK_PIN,
} from '../actions/app';
import MyBet from './MyBet';
import MyWallet from './MyWallet';
import UnlockPin from './UnlockPin';

const RoutingScreen = ({ screen }) => {
  switch (screen) {
    case SCREEN_MY_BET:
      return <MyBet />;
    case SCREEN_MY_WALLET:
      return <MyWallet />;
    case SCREEN_UNLOCK_PIN:
      return <UnlockPin />;
    default:
      return null;
  }
};

const mapStateToProps = state => ({
  screen: state.app.screen,
});

const mapDispatchToProps = {
  showNetwork,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RoutingScreen);
