import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import TextField from '@material-ui/core/TextField/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button/Button';
import Slider from '@material-ui/lab/Slider';

import { getActiveUserData } from '../utils/crypto';

import { loadFakePresetAssets, transferProxy } from '../utils/mixin';

import { hideAccounts, showAccounts } from '../actions/dashboard';
import { showSnackbar } from '../actions/snackbar';
import { reloadAccount, showBackupKeystore } from '../actions/wallet';

import sliderImg from '../images/slider.png';

import { getDiceRollUpCalculation } from '../utils/dice';

import { loadAssetsWithPreset } from '../utils/mixin';
import presetAssets from '../config/preset-assets';
import { setAssets } from '../actions/account';
import { SNACKBAR_SUCCESS, SNACKBAR_WARNING } from './WalletSnackbar';

import customTheme from './customTheme';

import { SCREEN_MY_WALLET, SCREEN_UNLOCK_PIN, setScreen } from '../actions/app';

const POOL_CLIENT_ID = '75d16668-0b6d-33b8-826e-1b130acaac6a';

const styles = {
  root: {
    width: 700,
    padding: '30px 50px 10px 10px',
  },
  slider: {
    padding: '30px 0px 20px 20px',
  },
  track: {
    height: 15,
    borderRadius: 25,
  },
  trackBefore: {
    height: 15,
    backgroundColor: '#cc0006',
    opacity: 1,
  },
  trackAfter: {
    height: 15,
    backgroundColor: '#00cc28',
    opacity: 1,
  },
  thumbIcon: {
    width: 25,
    height: 19,
  },
  textField: {
    color: '#fff',
    borderColor: '#fff',
  },
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssLabelGlowing: {
    color: '#000',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    textShadow:
      'rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px',
    borderColor: '#fff',
  },
  cssOutlinedInputReadOnly: {
    color: '#fff',
    textShadow:
      'rgb(5, 43, 0) 0px 0px 5px, rgb(0, 204, 40) 0px 0px 20px, rgb(0, 255, 76) 0px 0px 10px',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textShadow:
        'rgb(5, 43, 0) 0px 0px 5px, rgb(0, 204, 40) 0px 0px 20px, rgb(0, 255, 76) 0px 0px 10px',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
  },
};

class Dashboard extends React.Component {
  constructor(props) {
    super(props);

    const value = 50;
    const asset_id = props.assets[0].asset_id;
    const selectedAssetClass = props.assets[0];

    const memo = {
      g: 1, // dice
      n: value, // player selected number
    };
    const assetConfig = this.getAssetConfig(asset_id);

    this.state = {
      game: 1,
      sendTo: POOL_CLIENT_ID,
      sendAmount: '1',
      sendMemo: Buffer.from(JSON.stringify(memo)).toString('base64'),
      isLoading: false,
      selectedAsset: asset_id,
      selectedAssetClass: selectedAssetClass,
      value: value,
      lastResult: '00',
      lastSnapshotId: '',
      assetConfig: assetConfig,
    };
  }

  componentDidMount() {
    const { activeAccount } = this.props;
    if (activeAccount) {
      this.loadAssets(activeAccount);
    }
  }

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if (
      (this.props.activeAccount !== prevProps.activeAccount ||
        this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) &&
      this.props.activeAccount
    ) {
      this.loadAssets(this.props.activeAccount);
    }
  }

  loadAssets = async activeAccount => {
    const { setAssets } = this.props;
    if (!activeAccount) {
      const assets = await loadFakePresetAssets();
      setAssets(assets);
      this.setState({ assets });
      return;
    }
    const userData = await getActiveUserData(activeAccount);
    const assets = await loadAssetsWithPreset(userData);
    setAssets(assets);
    this.setState({ assets });
  };

  myWallet = () => {
    const { setScreen, mixinPin, activeAccount } = this.props;
    if (activeAccount) {
      if (!mixinPin) {
        const { setScreen } = this.props;
        setScreen(SCREEN_UNLOCK_PIN);
      } else {
        setScreen(SCREEN_MY_WALLET);
      }
    } else {
      setScreen(SCREEN_MY_WALLET);
    }
  };

  getAssetConfig = id => {
    let assetConfig = null;
    for (let i = 0; i < presetAssets.length; i++) {
      if (presetAssets[i].asset_id === id) {
        assetConfig = presetAssets[i];
        break;
      }
    }
    return assetConfig;
  };

  showAccounts = async event => {
    const { showAccounts } = this.props;
    showAccounts(event.currentTarget);
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  handleAmountChange = event => {
    const formattedAmount = event.target.value
      .replace(/^0+(?!\.|$)/, '')
      .replace(/[^0-9 .]+/g, '')
      .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1');

    this.setSendAmount(false, false, null, formattedAmount, null, null);
  };

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleAssetChange = event => {
    this.setState({ [event.target.name]: event.target.value });
    const { assets } = this.props;
    const selectedAssetClass = this.getSelectedAssetClass(
      event.target.value,
      assets
    );
    this.setState({ selectedAssetClass });

    const memo = {
      g: 1, // 1 = dice
      n: this.state.value, // player selected number
    };

    this.setState({ lastResult: '00', lastSnapshotId: '' });
    this.setState({
      sendMemo: Buffer.from(JSON.stringify(memo)).toString('base64'),
    });

    const assetConfig = this.getAssetConfig(event.target.value);
    this.setState({ assetConfig: assetConfig });

    this.setSendAmount(
      true,
      false,
      null,
      null,
      event.target.value,
      selectedAssetClass
    );
  };

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  handleSliderChange = (event, value) => {
    if (value < 4) {
      value = 4;
    }
    if (value > 97) {
      value = 97;
    }

    const memo = {
      g: 1, // 1=dice
      n: value, // player selected number
    };
    this.setState({ value });
    this.setState({ lastResult: '00', lastSnapshotId: '' });
    this.setState({
      sendMemo: Buffer.from(JSON.stringify(memo)).toString('base64'),
    });
  };

  rollMin = () => {
    this.setSendAmount(true, false, null, null, null, null);
  };
  rollMax = () => {
    this.setSendAmount(false, true, null, null, null, null);
  };
  roll2x = () => {
    this.setSendAmount(false, false, 2, null, null, null);
  };
  rollhalfx = () => {
    this.setSendAmount(false, false, 0.5, null, null, null);
  };

  getSelectedAssetClass = (asset_id, assets) => {
    let selectedAssetClass = null;
    assets.forEach(asset => {
      if (asset.asset_id === asset_id) {
        selectedAssetClass = asset;
      }
    });
    return selectedAssetClass;
  };

  padNumber(num, size) {
    return ('00' + num).substr(-size);
  }

  setSendAmount = (
    setMin,
    setMax,
    setTimes,
    newValue,
    selectedAsset,
    selectedAssetClass
  ) => {
    let asset = selectedAsset ? selectedAsset : this.state.selectedAsset;
    let assetClass = selectedAssetClass
      ? selectedAssetClass
      : this.getSelectedAssetClass(asset, this.props.assets);

    const balance = assetClass.balance || 0;
    const assetConfig = this.getAssetConfig(asset);
    if (assetConfig) {
      let amount = null;
      if (setMin) {
        amount = assetConfig.min;
        if (amount > balance) {
          amount = balance;
        }
      }

      if (setMax) {
        amount = assetConfig.max;
        if (amount > balance) {
          amount = balance;
        }
      }

      if (setTimes) {
        if (isNaN(this.state.sendAmount)) {
          amount = 0;
        } else {
          const floatAmount = parseFloat(this.state.sendAmount);
          amount = floatAmount * setTimes;

          if (amount < assetConfig.min) {
            amount = assetConfig.min;
          }
          if (amount > assetConfig.max) {
            amount = assetConfig.max;
          }
          if (amount > balance) {
            amount = balance;
          }
        }
      }

      if (newValue) {
        if (isNaN(newValue)) {
          amount = '0';
        } else {
          const floatAmount = parseFloat(newValue);
          amount = floatAmount;
          if (amount < assetConfig.min) {
            amount = assetConfig.min;
          }

          if (amount > assetConfig.max) {
            amount = assetConfig.max;
          }
          if (amount > balance) {
            amount = balance;
          }
        }
      } else {
        this.setState({ sendAmount: '0' });
      }
      if (amount) {
        this.setState({ sendAmount: amount.toString() });
      }
    } else {
      this.setState({ sendAmount: '0' });
    }
  };

  renderSending() {
    if (this.isSending()) {
      return (
        <div>
          <p>Rolling... Good Luck!</p>
        </div>
      );
    }

    return null;
  }

  rollToken = async () => {
    const { activeAccount, mixinPin } = this.props;

    if (!activeAccount) {
      const { setScreen } = this.props;
      setScreen(SCREEN_MY_WALLET);
      return null;
    }

    if (!mixinPin) {
      const { setScreen } = this.props;
      setScreen(SCREEN_UNLOCK_PIN);
      return null;
    }

    const { sendTo, sendAmount, sendMemo, selectedAsset } = this.state;
    if (sendAmount <= 0) {
      const { showSnackbar } = this.props;
      showSnackbar(`Insufficient fund, please deposit`, SNACKBAR_WARNING);
    } else {
      this.setState({ isLoading: true });
      let formattedSendAmount = sendAmount.toString();
      let that = this;

      const { assets, showSnackbar, reloadAccount } = this.props;
      const asset = assets.find(a => a.asset_id === selectedAsset);
      const remainingBalance = parseFloat(asset.balance);
      if (sendAmount > remainingBalance) {
        showSnackbar(`Insufficient fund, please deposit`, SNACKBAR_WARNING);
        return null;
      }
      const userData = await getActiveUserData(activeAccount);
      const sendData = {
        sendTo,
        sendAmount: formattedSendAmount,
        sendMemo,
        sendPin: mixinPin,
      };

      // console.log(sendData)

      try {
        showSnackbar('Rolling, please wait...');
        await transferProxy(asset, userData, sendData)
          .then(async function(data) {
            const snapshot_id = data.networkResponse.snapshot_id;

            const roll = data.rollResult.number;

            that.setState({
              isLoading: false,
              lastResult: roll,
              lastSnapshotId: snapshot_id,
            });
            reloadAccount();

            if (data.rollResult.win) {
              let payout = data.rollResult.win
                ? data.rollResult.payoutAmount
                : null;
              if (payout) {
                showSnackbar(
                  `You win! Receive payout ${payout}`,
                  SNACKBAR_SUCCESS
                );
              }
            } else {
              showSnackbar(`Bad Luck :(, please try again!`, SNACKBAR_WARNING);
            }
            reloadAccount();
          })
          .catch(function(e) {
            console.log(e);
            showSnackbar('Error');
            that.setState({
              isLoading: false,
              lastResult: '',
            });
            reloadAccount();
          });
      } catch (error) {
        this.setState({ isLoading: false });
        console.error(error);
        showSnackbar(`Transfer failed: ${error.description}`);
      }
    }
  };

  render() {
    const { assets, classes } = this.props;
    const { value, lastResult, lastSnapshotId } = this.state;

    const { sendTo, sendAmount, sendMemo, selectedAsset } = this.state;

    const selectedAssetClass = this.getSelectedAssetClass(
      selectedAsset,
      assets
    );

    let chance = '';
    let multiplier = '';
    let payout = 0;

    const diceSettings = getDiceRollUpCalculation(value);
    if (diceSettings) {
      chance = diceSettings.chance;
      multiplier = diceSettings.multiplier;
      payout = 0;
      if (chance && multiplier) {
        payout = multiplier * sendAmount;
      }
    }

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <div color="primary" className="card game-card">
            <Typography variant="h4" color="secondary">
              <span className="glowing-pink white">Roll over to win</span>
            </Typography>
            <div className="dice-slider">
              <Grid container spacing={24}>
                <Grid item xs={6} md={6} lg={6}>
                  <Grid container spacing={24}>
                    <Grid item xs={12} md={12} lg={12}>
                      <span className="your-number glowing-pink">
                        {this.padNumber(value, 2)}
                      </span>
                    </Grid>
                    <Grid item xs={12} md={12} lg={12}>
                      <span className="your-prediction glowing-pink">
                        Prediction
                      </span>
                    </Grid>
                  </Grid>
                </Grid>

                <Grid item xs={6} md={6} lg={6}>
                  <Grid container spacing={24}>
                    <Grid item xs={12} md={12} lg={12}>
                      <span className="lucky-number glowing-green">
                        {this.padNumber(lastResult, 2)}
                      </span>
                    </Grid>
                    <Grid item xs={12} md={12} lg={12}>
                      <span className="lucky-result glowing-green">
                        Lucky Number
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <div className={classes.root}>
                <Slider
                  classes={{
                    container: classes.slider,
                    track: classes.track,
                    trackBefore: classes.trackBefore,
                    trackAfter: classes.trackAfter,
                    thumbIcon: classes.thumbIcon,
                  }}
                  value={value}
                  min={1}
                  max={99}
                  step={1}
                  color="primary"
                  onChange={this.handleSliderChange}
                  thumb={
                    <div className="sliderThumbnail">
                      <img alt="Select your number" src={sliderImg} />{' '}
                      <span className="glowing-pink">{value}</span>
                    </div>
                  }
                />
              </div>
            </div>

            <div className="card send-card">
              <Grid container spacing={24}>
                <Grid item xs={12} md={6} lg={6}>
                  <TextField
                    select
                    label="Change Token"
                    name="selectedAsset"
                    className="send-token"
                    value={selectedAsset}
                    onChange={this.handleAssetChange}
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        notchedOutline: classes.notchedOutline,
                      },
                    }}
                    margin="normal"
                    color="secondary"
                    variant="outlined"
                  >
                    {assets.map(asset => (
                      <MenuItem key={asset.asset_id} value={asset.asset_id}>
                        <div className="send-asset-container">
                          <img
                            src={asset.icon_url}
                            alt={asset.symbol}
                            className="send-asset-icon"
                          />
                          <div className="send-asset-info">
                            {asset.balance || 0} {asset.symbol}
                          </div>
                        </div>
                      </MenuItem>
                    ))}
                  </TextField>
                  <TextField
                    required
                    name="sendAmount"
                    label="Bet Amount"
                    className="send-token"
                    value={sendAmount}
                    onChange={this.handleAmountChange}
                    margin="normal"
                    type="number"
                    placeholder="0.00"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        input: classes.cssOutlinedInput,
                        notchedOutline: classes.notchedOutline,
                      },
                      startAdornment: (
                        <img
                          src={selectedAssetClass.icon_url}
                          alt={selectedAssetClass.symbol}
                          className="send-asset-icon"
                        />
                      ),
                    }}
                    disabled={this.isSending()}
                    variant="outlined"
                  />

                  <Grid container spacing={0}>
                    <Grid item xs={12} md={12} lg={12}>
                      <Button
                        id="roll-min-button"
                        className="roll-quick-button"
                        variant="contained"
                        size="small"
                        color="primary"
                        onClick={this.rollMin}
                      >
                        <span className="glowing-pink">Min</span>
                      </Button>
                      <Button
                        id="roll-2x-button"
                        className="roll-quick-button"
                        variant="contained"
                        size="small"
                        color="primary"
                        onClick={this.roll2x}
                      >
                        <span className="glowing-pink">2x</span>
                      </Button>
                      <Button
                        id="roll-2x-button"
                        className="roll-quick-button"
                        variant="contained"
                        size="small"
                        color="primary"
                        onClick={this.rollhalfx}
                      >
                        <span className="glowing-pink">1/2</span>
                      </Button>

                      <Button
                        id="roll-max-button"
                        className="roll-quick-button"
                        variant="contained"
                        size="small"
                        color="primary"
                        onClick={this.rollMax}
                      >
                        <span className="glowing-pink">Max</span>
                      </Button>
                    </Grid>
                  </Grid>

                  <Button
                    id="send-token-button"
                    className="actionbutton"
                    variant="contained"
                    color="secondary"
                    size="large"
                    onClick={this.rollToken}
                    disabled={this.isSending()}
                  >
                    Roll Over {value}
                  </Button>

                  <Button
                    id="switch-wallet-button"
                    className="actionbutton"
                    variant="contained"
                    color="primary"
                    size="large"
                    disabled={this.isSending()}
                    onClick={this.myWallet}
                  >
                    <span className="glowing-green">Withdraw / Deposit</span>
                  </Button>
                </Grid>
                <Grid item xs={12} md={6} lg={6}>
                  <TextField
                    required
                    name="chance"
                    label="Chance to win"
                    className="send-to"
                    autoComplete="off"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        input: classes.cssOutlinedInputReadOnly,
                      },
                    }}
                    value={(chance * 100).toFixed(2) + '%'}
                    margin="normal"
                  />

                  <TextField
                    required
                    name="multiplier"
                    label="Multiplier"
                    className="send-to"
                    autoComplete="off"
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        input: classes.cssOutlinedInputReadOnly,
                      },
                    }}
                    value={multiplier + ' x'}
                    margin="normal"
                  />

                  <TextField
                    required
                    name="payout"
                    label="Payout on win "
                    className="send-to"
                    autoComplete="off"
                    value={payout}
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      classes: {
                        root: classes.cssOutlinedInput,
                        input: classes.cssOutlinedInputReadOnly,
                      },
                      startadornment: (
                        <img
                          src={selectedAssetClass.icon_url}
                          alt={selectedAssetClass.symbol}
                          className="send-asset-icon"
                        />
                      ),
                    }}
                    margin="normal"
                  />

                  <TextField
                    name="lastSnapshotId"
                    label=""
                    className="send-to"
                    autoComplete="off"
                    value={lastSnapshotId}
                    InputLabelProps={{
                      classes: {
                        root: classes.cssLabel,
                      },
                    }}
                    InputProps={{
                      readOnly: true,
                      classes: {
                        root: classes.cssOutlinedInput,
                        input: classes.cssOutlinedInputReadOnly,
                      },
                    }}
                    margin="normal"
                  />

                  <div className="hidden">
                    <TextField
                      required
                      inputProps={{
                        readOnly: true,
                      }}
                      name="sendTo"
                      label="Miwin Client ID"
                      className="send-to"
                      autoComplete="off"
                      value={sendTo}
                      onChange={this.handleChange}
                      margin="normal"
                      disabled={this.isSending()}
                    />

                    <TextField
                      name="sendMemo"
                      label="Memo"
                      className="send-memo"
                      value={sendMemo}
                      margin="normal"
                      inputProps={{ maxLength: 140 }}
                      disabled={this.isSending()}
                    />

                    {this.renderSending()}
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapStateToProps = state => ({
  activeAccount: state.account.activeAccount,
  assets: state.account.assets,
  reloadAccountNonce: state.wallet.reloadAccountNonce,
  sendTokenOpen: state.wallet.sendTokenOpen,
  accountsOpen: state.dashboard.accountsOpen,
  mixinPin: state.wallet.mixinPin,
});

const mapDispatchToProps = {
  showSnackbar,
  showAccounts,
  hideAccounts,
  showBackupKeystore,
  reloadAccount,
  setScreen,
  setAssets,
};

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Dashboard));
