import CryptoJS from 'crypto-js';

export const getDiceRollUpCalculation = i => {
  let s = null;

  if (!isNaN(i)) {
    const a = 1;
    const b = 0;

    const r = 100;
    const c = 4;
    const d = 98;

    if (i >= c && i <= d) {
      s = {};
      var e = r - (i - b) - 1;
      var p = e / r;
      var m = 1 + (i - b - a) / e;

      s.chance = p;
      s.multiplier = parseFloat(m).toFixed(3);
    }
  }

  return s;
};
export const calculateHashRandom = data => {
  try {
    const hash = CryptoJS.SHA512(data);
    const buffer = Buffer.from(hash.toString(CryptoJS.enc.Hex), 'hex');
    const arr = new Uint8Array(buffer);

    let number = 0;
    for (let i = 0; i < arr.length; i += 1) {
      number += arr[i];
    }

    const integer = number % 100;
    return integer;
  } catch (e) {
    return null;
  }
};
