import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import Typography from '@material-ui/core/Typography/Typography';
import Button from '@material-ui/core/Button/Button';
import FormControl from '@material-ui/core/FormControl';
import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import IconButton from '@material-ui/core/IconButton/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField/TextField';
import LockOpen from '@material-ui/icons/LockOpen';

import { withStyles } from '@material-ui/core/styles';

import AES from 'crypto-js/aes';

import {
  hideImportPrivateKey,
  importPrivateKey,
  setMixinPin,
} from '../actions/wallet';

import Transition from './Transition';
import { isPrivateKeyValid } from '../utils/crypto';
import { backgroundPage } from '../utils/backgroundPage';
import { localStorage } from '../utils/localStorage';
import { showSnackbar } from '../actions/snackbar';
import { setAccountInfo } from '../actions/account';
import { SCREEN_MY_BET, setScreen } from '../actions/app';
import { verifyPin } from '../utils/mixin';
import { SNACKBAR_ERROR } from './WalletSnackbar';
import { initWalletStorage } from '../utils/wallet';

const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};

class ImportPrivateKey extends Component {
  constructor(props) {
    super(props);
    this.state = {
      privateKey: '',
      importPin: '',
    };
  }

  disableSubmit = () => {
    const { privateKey, importPin } = this.state;
    return !isPrivateKeyValid(privateKey) || !importPin || importPin.length < 6;
  };

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleSubmit = event => {
    event.preventDefault();
    this.importPrivateKey();
  };

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    if (value.length > 6) {
      value = value.substring(0, 6);
    }
    this.setState({ [event.target.name]: value });
  };

  importPrivateKey = async () => {
    const {
      setAccountInfo,
      hideImportPrivateKey,
      showSnackbar,
      setScreen,
      setMixinPin,
    } = this.props;
    const { privateKey, importPin } = this.state;

    const data = JSON.parse(
      Buffer.from(privateKey.trim(), 'base64').toString()
    );
    const userData = {
      clientId: data.uid,
      pinToken: data.pintoken,
      sessionId: data.sid,
      privateKey: data.key,
    };

    try {
      await verifyPin(importPin, userData);
      setMixinPin(importPin);

      await initWalletStorage();

      const clientId = userData.clientId;
      const passwordHashInBackground = await backgroundPage.getPasswordHash();
      const accounts = await localStorage.getAccounts();
      const encryptedUserData = AES.encrypt(
        JSON.stringify(userData),
        passwordHashInBackground
      ).toString();

      const accountExisted =
        accounts.filter(account => account.clientId === clientId).length > 0;
      if (accountExisted) {
        showSnackbar('Account already exists! Please sign into another one.');
        return;
      }

      const activeAccount = {
        clientId,
        encryptedUserData,
      };

      accounts.push(activeAccount);
      await localStorage.setAccounts(accounts);

      await backgroundPage.setActiveAccount(activeAccount);
      setAccountInfo(accounts, activeAccount);
      showSnackbar('Sign in successfully!');

      setScreen(SCREEN_MY_BET);
      hideImportPrivateKey();
    } catch (e) {
      console.error(e);
      showSnackbar(`Fail to sign in: ${e.description}`, SNACKBAR_ERROR);
    }
  };

  render() {
    const { hideImportPrivateKey, open, classes } = this.props;
    const { privateKey, importPin, showPassword } = this.state;
    return (
      <Dialog
        fullScreen
        aria-labelledby="switch-network-title"
        open={open}
        onClose={hideImportPrivateKey}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={hideImportPrivateKey}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              <span className="white">Sign in LuckyBet</span>
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
          <div className="card">
            <div className="card signin-card">
              <Typography variant="h6">
                <span className="white glowing-pink">
                  Enter private key and PIN
                </span>
              </Typography>
              <p className="import-private-key-description">
                Private key is encrypted and only stored in local Chrome
                storage. We do not store your private key or PIN on the server.
              </p>
              <TextField
                name="privateKey"
                label="Private key"
                className="private-key-field"
                multiline
                rows="8"
                autoComplete="off"
                value={privateKey}
                onChange={this.handleChange}
                helperText=""
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  maxLength: 6,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                  //className: classes.textField,
                }}
                variant="outlined"
                margin="normal"
              />
              <FormControl className="import-pin" margin="normal">
                <TextField
                  required
                  label="6 digit PIN"
                  name="importPin"
                  autoComplete="off"
                  type={showPassword ? 'text' : 'password'}
                  value={importPin}
                  onChange={this.handlePinChange}
                  placeholder="6 digit PIN"
                  InputLabelProps={{
                    classes: {
                      root: classes.cssLabel,
                    },
                  }}
                  InputProps={{
                    width: 20,
                    maxLength: 6,
                    classes: {
                      root: classes.cssOutlinedInput,
                      notchedOutline: classes.notchedOutline,
                    },
                    //className: classes.textField,
                  }}
                  variant="outlined"
                />
              </FormControl>
              <div className="space" />
              <Button
                className="sign-in-button button actionbutton"
                color="secondary"
                variant="contained"
                disabled={this.disableSubmit()}
                onClick={this.handleSubmit}
              >
                Sign in <LockOpen className="account-details-button-icon" />
              </Button>
              <div className="space" />
            </div>
          </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.importPrivateKeyOpen,
});

const mapDispatchToProps = {
  hideImportPrivateKey,
  importPrivateKey,
  setAccountInfo,
  showSnackbar,
  setScreen,
  setMixinPin,
};

ImportPrivateKey.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(ImportPrivateKey));
